$('document').ready(() => {
    // troca a background color da navbar ao clicar no botão de expandir
    $('#hamburger-menu').click(() => {
        setTimeout(() => {
            var height = $('nav').height()
            if (height > 150) {
                $('nav').css('background-color', '#0b5be4')
            } else {
                $('nav').css('background-color', '')
            }
        }, 200);
    })

    //  troca a cor da navbar ao descer
    $(window).scroll(() => {
        // não utilizar o this pq buga no parcel build            AQUI --V-- AQUI
        $('nav').toggleClass('nav-after-scroll animated fadeInDown', $(window).scrollTop() > $('nav').height())
    })

    //
    //
    //
    //
    // OWL TROCAR PARA OUTRO JS DEPPOIS
    $('.owl-carousel').owlCarousel({
        loop: true,
        margin: 10,

        responsive: {
            0: {
                items: 1
            },
            600: {
                items: 3
            },
            1000: {
                items: 5
            }
        }
    })


    //
    //
    //
    //
    // PARALLAX TROCAR PARA OUTRO JS DEPOIS
    $(window).scroll(function () {
        var top_of_element = $(".parallax").offset().top;
        var bottom_of_element = $(".parallax").offset().top + $(".parallax").outerHeight();
        var bottom_of_screen = $(window).scrollTop() + $(window).innerHeight();
        var top_of_screen = $(window).scrollTop();

        if ((bottom_of_screen > top_of_element) && (top_of_screen < bottom_of_element)) {
            $("#parallax-content").addClass('animated bounceInUp')
        } else {
            $("#parallax-content").removeClass('animated bounceInUp')
        }
    });



    //
    //
    //
    //
    // request info

    $('#duvidas-btn').click((e) => {
        e.preventDefault()
        var dados = $('#duvidas').serialize();
        $('#msg').html('')

        $.ajax({
            type: 'GET',
            url: './php/sendmail.php',
            data: dados, // x-www-form-urlencoded
            success: e => {
                var textcolor = 'text-danger'
                if (e == 'E-mail enviado com sucesso!') {
                    textcolor = 'text-success'
                    $('#duvidas input').val('')
                }
                var msg = `<h4 class="text-center ${textcolor} mt-3">${e}</h4>`
                $('#msg').append(msg)
                $('#msg h4').fadeOut(3000)
            },
            error: e => {
                var msg = `<h4 class="text-center text-danger mt-2">Não foi possível envial este e-mail! Por favor tente novamente mais tarde.</h4>`
                $('#msg').append(msg)
            }
        })
    })




    var pathname = window.location.href
    if (pathname.includes('erroenvioformulario')) {
        let error = '<h4 class="text-center text-danger mt-2">Ops! ocoreu um erro, tente novamente mais tarde!</h4>'
        $('#request').append(error)
    } else if (pathname.includes('enviadocomsucesso')) {
        let success = '<h4 class="text-center text-success mt-2">Mensagem enviada com sucesso!</h4>'
        $('#request').append(success)
    }




})